const sliders = (slides, dir, prev, next) => {

    let slideIndex = 1,

        paused = true;

    const items = document.querySelectorAll(slides);

    function showSlides(n) {
        if (n > items.length) {                             // Vozvrat na 1-yi slide pri dostigenii konca //
            slideIndex = 1;
        }

        if (n < 1) {                                        // Perenos v konec pri dostigenii nanchala //
            slideIndex = items.length;
        }

        items.forEach(item => {
            item.classList.add("animated");                 // Dobablyaet vsem slides class "animated" //
            item.style.display = "none";                    // Skrivaet vse slides //
        });

        items[slideIndex - 1].style.display = 'block';      // Zapusk animation s 0-ogo slide //
    }

    showSlides(slideIndex);

    function plusSlides(n) {                                // Peremennay "n" zadaet napravlenie //
        showSlides(slideIndex += n);
    }

    try {                                                   // V Sluchae prisutstviya peremennix 'prev' i 'next' //
        const prevBtn = document.querySelector(prev),
              nextBtn = document.querySelector(next);


        prevBtn.addEventListener('click', () => {
            plusSlides(-1);                                                 // Zadaet napravlenie //

            items[slideIndex - 1].classList.remove('slideInLeft');
            items[slideIndex - 1].classList.add('slideInRight');            // Zadaet napravlenie tekushemu slide //

        });

        nextBtn.addEventListener('click', () => {
            plusSlides(1);                                                  // Zadaet napravlenie //

            items[slideIndex - 1].classList.remove('slideInRight');
            items[slideIndex - 1].classList.add('slideInLeft');             // Zadaet napravlenie tekushemu slide //

        });

    } catch(e){}

    function activateAnimation() {                                      

        if (dir === 'vertical') {                                           // in case "vertical" //
            
            paused = setInterval(function() {
                plusSlides(1);
                items[slideIndex - 1].classList.add('slideInDown');         // Zadaet napravlenie tekushemu slide //
            }, 3000);
        
        } else {
            
            paused = setInterval(function() {                               // in case "horizontal" //
                plusSlides(1);
                items[slideIndex - 1].classList.remove('slideInRight');
                items[slideIndex - 1].classList.add('slideInLeft');         // Zadaet napravlenie tekushemu slide //
            }, 3000);
        }

    }
    activateAnimation();                                          // Zapusk 'Animation' //

    items[0].parentNode.addEventListener('mouseenter', () => {          // V sluchae 'mouseenter' v "ParentNode" slida //

        clearInterval(paused);                                          // Ostanavlivaet tekushiy "paused = setInterval" //                                          
    });

    items[0].parentNode.addEventListener('mouseleave', () => {          // V sluchae 'mouseleave' iz "ParentNode" slida //
        
        activateAnimation();                                      // Povtorniy Zapusk 'Animation' //            
    });

};

export default sliders;
